import config from "../../config.js"
export default {
	data() {
		return {
			latitude: 39.909,
			longitude: 116.39742,
			markers: [],
			title: '当前消息',
			thisloc_markers: [],
			text: '',
		}
	},
	onReady(){
		try {
			this.latitude = uni.getStorageSync('storage_key_latitude');
			this.longitude = uni.getStorageSync('storage_key_longitude');
			console.log("当前坐标");
			console.log(this.longitude);
			console.log(this.latitude);
		} catch (e) {
			console.log('onLoad_error');
			// error
		};
	},
	onLoad() {
		
		let data = {
			userId: "Hello",
			loc: {
				log: this.longitude,
				lat: this.latitude
			},
			period: {
				start: -1000 * 60 * 60 * 24,
				end: 0
			},
			range: 0.01,
		}
		const that = this;
		uni.request({
			url: `${config.HOST}/${config.API.MESSAGE}/get`,
			method: "post",
			dataType: "json",
			data: data,
			success(res) {
				const {
					data
				} = res;

				if (!data.ok) {
					return;
				}

				const {
					results
				} = data;

				let marks = [];
				for (let i = 0; i < results.length; i++) {
					const msg = results[i];
					marks.push({
						latitude: msg.loc.y,
						longitude: msg.loc.x,
						content: msg.msg,
						id: msg.userId,
					});
				}
				for (var key in marks) {
				    if (marks[key].longitude === that.longitude && marks[key].latitude === that.latitude) {
						that.markers.push(marks[key])
				    }
				 }
			},
			fail(err) {
				uni.showToast({
					title: "消息获取失败",
					icon: "none"
				})
			}
		});
		/*wx.showShareMenu({
			showShareItems:['wechatFriends', 'wechatMoment']
		});*/
	},
	methods: {
		sure(){
			if(this.text == ''){
				uni.showModal({
					showCancel: false,
					confirmText: '知道了',
					title: '提示',
					content: '内容为空，记得写点东西哦',
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} 
					}
				});
			}
			else{
				uni.showToast({
					title: "发送完成",
				});
				const that = this;
				let data = {
					userId: "Hello",
					loc: {
						log: this.longitude,
						lat: this.latitude
					},
					msg: this.text,
				}

				uni.request({
					url: `${config.HOST}/${config.API.MESSAGE}/send`,
					method: "post",
					dataType: "json",
					data: data,
					success(data) {
						that.message = ""
					},
					fail(err) {
						uni.showToast({
							title: "发送失败，请稍后再试",
							icon: "none"
						})
					}
				});
				that.markers.push({
					latitude: this.longitude,
					longitude: this.latitude,
					content: this.text,
				})
				this.text = '';
			}
		}
	}
}
